<script>
export default {
  data() {
    return {
      loadFooter: true,
    };
  },
  watch: {
    '$route.path': {
      handler() {
        if (this.$route.path === '/cart') {
          this.loadFooter = false;
          setTimeout(() => {
            this.loadFooter = true;
          }, 1000);
        }
      },
      immediate: true,
    },
  },
  created() {
    if (this.$route.path === '/cart') this.loadFooter = true;
    if (this.$route.path === `/order/${this.$route.params.id}`) {
      this.loadFooter = false;
      setTimeout(() => {
        this.loadFooter = true;
      }, 300);
    }
  },
};
</script>
